import React from "react";
import ListItem from "./ListItem";

const GraphList = ({ list, onDelete, onClone, name }) => {
  if (!list || list.length < 1)
    return <h6 className="text-center my-2 text-secondary">–°–ø–∏—Å–æ–∫ –ø—É—Å—Ç–∏–π</h6>;
  const items = list
    .sort((a, b) => new Date(b.created) - new Date(a.created))
    .map((item, index) => (
      <ListItem
        key={item.id}
        item={item}
        index={index}
        onDelete={onDelete}
        onClone={onClone}
        name={name}
      />
    ));
  return (
    <React.Fragment>
      <table className="table">
        <thead>
          <tr>
            <th style={{ width: "10%" }}>#</th>
            <th style={{ width: "30%" }}>–Ü–º'—è</th>
            <th style={{ width: "25%" }}>–°—Ç–≤–æ—Ä–µ–Ω–æ</th>
            <th style={{ width: "25%" }}>–û–Ω–æ–≤–ª–µ–Ω–æ</th>
            <th style={{ width: "10%" }}>–û–ø–µ—Ä–∞—Ü—ñ—ó</th>
          </tr>
        </thead>
        <tbody>{items}</tbody>
      </table>
    </React.Fragment>
  );
};

export default GraphList;
