import React, { Component, createRef } from "react";
import Queue from "../models/Queue";
import QueueResultTable from "./QueueResultTable";

class QueueView extends Component {
  constructor(props) {
    super(props);
    this.resultsRef = createRef();
    this.state = {
      data: props.data,
      results: [],
      algo: -1,
      description: {
        1: `–£ –ø–æ—Ä—è–¥–∫—É —Å–ø–∞–¥–∞–Ω–Ω—è –ø—Ä–æ–Ω–æ—Ä–º–æ–≤–∞–Ω–æ—ó —Å—É–º–∏ –∫—Ä–∏—Ç–∏—á–Ω–∏—Ö –ø–æ —á–∞—Å—É —ñ –ø–æ –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –≤–µ—Ä—à–∏–Ω —à–ª—è—Ö—ñ–≤ –¥–æ –∫—ñ–Ω—Ü—è –≥—Ä–∞—Ñ–∞ –∑–∞–¥–∞—á—ñ.`,
        2: `–£ –ø–æ—Ä—è–¥–∫—É —Å–ø–∞–¥–∞–Ω–Ω—è –∫—Ä–∏—Ç–∏—á–Ω–æ–≥–æ —á–∞—Å—É –¥–æ –∫—ñ–Ω—Ü—è –≥—Ä–∞—Ñ—É`,
        11: `–£ –ø–æ—Ä—è–¥–∫—É —Å–ø–∞–¥–∞–Ω–Ω—è –∑–≤‚Äô—è–∑–Ω–æ—Å—Ç—ñ –≤–µ—Ä—à–∏–Ω, –∞ –ø—Ä–∏ —Ä—ñ–≤–Ω–∏—Ö –∑–Ω–∞—á–µ–Ω–Ω—è—Ö
              ‚Äì –≤ –ø–æ—Ä—è–¥–∫—É –∑—Ä–æ—Å—Ç–∞–Ω–Ω—è –∫—Ä–∏—Ç–∏—á–Ω–æ–≥–æ –ø–æ —á–∞—Å—É —à–ª—è—Ö—ñ–≤ –≤—ñ–¥ –ø–æ—á–∞—Ç–∫—É –≥—Ä–∞—Ñ–∞
              –∑–∞–¥–∞—á—ñ. `
      }
    };
  }

  handleOptionChange = e => {
    const algo = parseInt(e.target.value);
    this.setState({ algo });
    const queue = new Queue(algo, this.props.data);
    const results = queue.run();
    window.scrollTo(0, this.resultsRef.current.offsetTop);
    this.setState({ results });
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.state.results.length > 0) {
      window.scrollTo(0, this.resultsRef.current.offsetTop);
    }
  }

  render() {
    const { results, algo, description } = this.state;
    return (
      <React.Fragment>
        <div className="form-group">
          <label htmlFor="algo">
            <h5>–ê–ª–≥–æ—Ä–∏—Ç–º: </h5>
          </label>
          <select
            className="custom-select"
            name="algo"
            value={algo}
            onChange={this.handleOptionChange}
          >
            <option value="-1" disabled>
              –û–±—Ä–∞—Ç–∏
            </option>
            {Object.keys(description).map(i => {
              return (
                <option value={i} key={i}>
                  {i}
                </option>
              );
            })}
          </select>
        </div>
        <div ref={this.resultsRef}>
          {algo > 0 && (
            <div>
              <h5>–û–ø–∏—Å –∞–ª–≥–æ—Ä–∏—Ç–º—É: </h5>
              {description[algo]}
              <h5 className="my-2">–†–µ–∑—É–ª—å—Ç–∞—Ç: </h5>
              <QueueResultTable data={results} />
            </div>
          )}
        </div>
      </React.Fragment>
    );
  }
}

export default QueueView;
